<template>
    <PDHeader></PDHeader>

    <v-main>
        <div>
            <canvas id="chart"></canvas>
        </div>
    </v-main>

    <PDFooter></PDFooter>
</template>


<script setup>
import PDHeader from '@/components/PD-Header.vue';
import PDFooter from '@/components/PD-Footer.vue';
import { onMounted } from 'vue'
import Chart from 'chart.js/auto'

const data = [
    { year: 2010, count: 10 },
    { year: 2011, count: 20 },
    { year: 2012, count: 15 },
    { year: 2013, count: 25 },
    { year: 2014, count: 22 },
    { year: 2015, count: 30 },
    { year: 2016, count: 80 },
];

onMounted(() => {
    new Chart(
        document.getElementById('chart'),

        {
            type: 'line',
            data: {
                labels: data.map((data) => {
                    return data.year
                }),
                datasets: [
                    {
                        label: '年度變化',
                        data: data.map((data) => {
                            return data.count
                        })
                    }
                ],


            },
            options: {
                plugins: {
                    legend: {
                        labels: {
                            // This more specific font property overrides the global property
                            font: {
                                size: 14,
                                weight:'bold'
                            }
                        }
                    }
                }
            }

        }
    );
})



</script>